<template>
<div class="commenter">

<div v-if="sudahComment">
    Thanks for your thoughts! If your comment is approved, it will appear here very soon! :)
    <div class="comment">
        <h3> {{commenterName}} says: </h3>
            <p>{{newComment}}</p>
            <em><p> On {{currDate}}</p></em>
    </div>
</div>
<div v-else class="hero">
  <div class="heroBox">
    <form class="pure-form">
      <fieldset>
        <legend>Leave a Comment</legend>
        <input placeholder="Name" v-model="commenterName">
        <br>
        <br>
        <input placeholder ="Email" v-model = "commenterEmail">
        <br>
        <br>
        <textarea placeholder = "Comment..." v-model = "newComment">  </textarea>
        <star-rating v-model=newRating v-bind:increment="1" v-bind:active-on-click=true v-bind:star-size="20" active-color="gold" v-bind:show-rating=false></star-rating>

    
        
      </fieldset>
      
      <fieldset>
        <button type="submit" class="pure-button pure-button-primary" @click.prevent="addComment">Submit</button>
      </fieldset>
    </form>
    
  </div>
</div>


<div v-for="comment in comments" :key="comment.id">
    <div class="comment">
        <h3> {{comment.name}} says: </h3>
            <p>{{comment.comment}}</p>
            <em><p> On {{comment.date}}</p></em>
    </div>
</div>
</div>
  
</template>

<script>
// @ is an alias to /src


export default {
  name: 'Commenter',
  components: {
      
  },
  methods:{
      addComment(){
          this.$root.$data.db.collection("comments").add({
            name: this.commenterName,
            email: this.commenterEmail,
            comment: this.newComment,
            date: this.currDate,
            page: this.$route.params.id,

        })
        .then((docRef) => {
            console.log("Document written with ID: ", docRef.id);
        })
        .catch((error) => {
            console.error("Error adding document: ", error);
        });
        this.sudahComment = true;
        if (this.newRating != 0){ //Assuming they didn't try the recipe and just commenting, don't want to affect stats
            let averageRating = 0;
            let numRatings = 0;
            var docRef =this.$root.$data.db.collection("recipes").doc(this.$route.params.id); // Id is 302, 303 etc
            docRef.get().then((doc) => {
                if (doc.exists) {
                averageRating = doc.data().rating;
                numRatings = doc.data().totalRatings; 
                this.$root.$data.db.collection("recipes").doc(this.$route.params.id)
                .set({rating: (((averageRating*numRatings) +this.newRating)/ (numRatings+1)), totalRatings : numRatings+1});
            } else {
                //doc.data is undefined, create a recipe in the thing
                    this.$root.$data.db.collection("recipes").doc(this.$route.params.id).set({rating:0, totalRatings:0});
                    return 5;
                }
            }).catch((error)=>{
                console.log("error getting doc: ", error);
            });
         }
          //Great, it exists, now reset it!
         





      },
  },
  data(){
      return {
          newComment:'',
          commenterName: '',
          date: this.currDate,
          comments:[{comment: "Wow what a recipe", name: "Rachel", date: "May 12, 2021"}],
          commenterEmail: '',
          sudahComment: false,
          newRating: 0,
      }
  },
  computed:{
      currDate(){
          let currentDate = new Date();
            let Day = currentDate.getDate();
            let Month = currentDate.getMonth() + 1;
            let Year = currentDate.getFullYear();
            let curr_date = Month + "/" + Day +"/" + Year;
          return curr_date;
      }
  }
}
</script>










