<script>

import Commenter from '../components/Commenter.vue'
export default {
  name: 'RecipeIndex',
  components: {
      Commenter,
  },
  
  data(){
      return {
          recipes: this.$root.$data.recipes,
          recipe:null,
          photopath: '',
          photopath2:'',
          avgRating : 0,
      }
  },
  created(){
      
      this.getRecipe();
      this.photopath=this.recipe.img;
      this.getRating();
      this.scrollToTop();
  },
  computed:{
    
  },

  methods:{
    getRating(){
          var docRef =this.$root.$data.db.collection("recipes").doc(this.$route.params.id); // Id is 302, 303 etc
          docRef.get().then((doc) => {
            if (doc.exists) {
              this.avgRating= doc.data().rating;
              
          } else {
              console.log('did not find it')
              //doc.data is undefined, create a recipe in the thing
                this.$root.$data.db.collection("recipes").doc(this.$route.params.id).set({rating:0, totalRatings:0});
                this.avgRating = 0;
            }
          }).catch((error)=>{
              console.log("error getting doc: ", error);
          });
        console.log(this.avgRating)

          
          
      },
      scrollToTop() {
            window.scrollTo(0,0);
        },
      getRecipe(){
          console.log(this.$route.params.id)
          
          let recipes = this.$root.$data.recipes;
          recipes.forEach(value => {
            if (value.id ==this.$route.params.id){
                this.recipe = value;
                this.photopath = this.recipe.img;
                if (this.recipe.img2 != null){
                    this.photopath2 = this.recipe.img2
                }
                return;
            }

          
          });
      }
  },
}
</script>
