<script>
// @ is an alias to /src

export default {
  
  components: {
      
  },
  created() {
    window.addEventListener("resize", this.closeMenu);
    
},
  mounted() {
    window.addEventListener(
      "orientationchange",
      this.loglog
    );
  },

    directives: {
        
        'togglemenudirective': {
            bind: function(el) {
                 el.addEventListener('click', () => {
                        console.log('bind')
                        el.classList.toggle('custom-menu-tucked');
                });
            }
        }
    },
   methods:{
     closeMenu() 
     {  try {
          if (this.$refs["menu"].classList.contains('open')) {
              this.toggleMenu();
          }
        }
        catch(error){
            console.log('error')
        }
      },


    loglog(){
        console.log("whoa there");
    },
     toggleHorizontal() {
        if (this.$refs["menu"].classList.contains('open')){
            this.$refs["menu"].classList.remove('closing');
            this.$refs["can"].classList.toggle('pure-menu-horizontal');
        }
        /*[].forEach.call(this.$refs["menu"].querySelector('.custom-can-transform'),
              function(el){
                  console.log(el)
                  el.classList.toggle('pure-menu-horizontal');
              }
          );*/
          
         /*
         this.$refs["toggle-x"].classList.toggle('x');
         this.$refs["tuckMenu"].classList.toggle('custom-menu-tucked');*/
     },
     toggleMenu() {
        this.$refs["can"].classList.toggle('pure-menu-horizontal');
        
         if (this.$refs["menu"].classList.contains('open')) {
              this.$refs["menu"].classList.add('closing');
            //..rollBack = setTimeout(this.toggleHorizontal, 200);
         }
          /*
          else {
              if (this.$refs["menu"].classList.contains('closing')) {
                  clearTimeout(rollBack);
                  console.log("supposed to be clearing timeout...");
              } else {
                  this.toggleHorizontal();
              }
          }*/
          this.$refs["menu"].classList.toggle('open');
          this.$refs["toggle"].classList.toggle('x');
     },
      filterRecipes() {
           let filtered = []
           this.$root.$data.recipes.forEach(recipe => {
               recipe.keywords.every(keyword => {
                    console.log(keyword)
                    if (keyword.search(this.searchText.toLowerCase())>=0){
                        console.log(keyword)
                        filtered.push(recipe)
                        return false;
                    }
                    else{
                        return true;
                    }
               });
               
           });
           /*this.$root.$data.filtered = this.$root.$data.recipes.filter(recipe => recipe.keywords.includes(this.searchText.toLowerCase()));*/
           this.$root.$data.filtered = filtered;
           this.$root.$data.searching = true;
           this.$router.push({
                name: 'Search',
                params: { data: this.searchText}
            });
           /*this.$router.push('/search-results/' + this.searchText);*/
      }
  },
  data(){
      return {
          searchText:'',
      }
  }
}
</script>


